/**
 * Wordpress Dependencies
 */
import { useDispatch } from "@wordpress/data";
/**
 * Internal Dependencies
 */
import { allSteps } from "@/admin/features/onboard/constants";
import { stores } from "@/globalConstant";
const useOnboardservice = () => {
    const { setOnboardState } = useDispatch(stores === null || stores === void 0 ? void 0 : stores.onboard);
    /**
     * Handles the selection and deselection of a module in a multi-select list,
     * updates the step flow based on selected modules.
     */
    const handleModuleSelection = ({ selectedValue, value = [], onChange, }) => {
        // Check if the selected value is already in the list
        const exists = value.includes(selectedValue);
        // If it exists, remove it (deselect); otherwise, add it (select)
        const newValue = exists
            ? value.filter((v) => v !== selectedValue)
            : [...value, selectedValue];
        // Trigger the update with the new list of selected modules
        onChange(newValue);
        // Define the available modules that can impact step rendering
        const modules = ["food_ordering", "reservation"];
        // Find which expected modules are missing from the new selection
        const missingModules = modules.filter((item) => !newValue.includes(item));
        // Filter out steps associated with missing modules
        const updatedSteps = allSteps.filter((step) => !missingModules.includes(step.key));
        // Update onboarding state with the new step list and total count
        setOnboardState({
            steps: updatedSteps,
            totalSteps: updatedSteps.length,
        });
    };
    return { handleModuleSelection };
};
export default useOnboardservice;
